<template>
  <div style="width:100%">
    <!-- 顶部toolbar -->
    <el-form ref="form1" :inline="true" class="toolbar" label-position="right" label-width="100px" :model="queryStr">
      <el-row :gutter="24">
        <el-form-item label="公司名称" class="ml15" prop="company_name">
          <el-input v-model.trim="queryStr.company_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click = "query(1)" >查询</el-button>
          <el-button type="primary" @click="newAdd" >新建</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <!-- 列表 -->
    <el-table :data="tableData" border v-loading="loading">
     <el-table-column type="index" width="50" label="序号">
      </el-table-column>
			<el-table-column header-align="center" align="center" label="操作"  min-width="150">
				<template slot-scope="scope">
					<el-button type="danger" size="mini" @click = "edit_query(scope.row, scope.$index)">修改</el-button>
          <el-button type="primary" size="mini" @click = "add_query(scope.row, scope.$index)">新增管理员</el-button>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="公司名称"  >
				<template slot-scope="scope">
					<span>{{scope.row.company_name}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="公司id"  >
				<template slot-scope="scope">
					<span>{{scope.row.company_id}}</span>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="联系人"  >
				<template slot-scope="scope">
					<span>{{scope.row.company_contact}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="联系方式"  >
				<template slot-scope="scope">
					<span>{{scope.row.company_tel}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="备注"  >
				<template slot-scope="scope">
					<span>{{scope.row.company_remarks}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="创建时间"  >
				<template slot-scope="scope">
					<span>{{scope.row.create_time |time}}</span>
				</template>
			</el-table-column>
       <el-table-column header-align="center" align="center" label="更新时间"  >
				<template slot-scope="scope">
					<span>{{scope.row.update_time |time}}</span>
				</template>
			</el-table-column>
    
		</el-table>
    <!-- 底部toolbar -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="prev, pager, next, jumper" @current-change="currentChange" :page-size="10" :total="queryStr.total" style="float:right;">
				</el-pagination>
			</el-col>
    <!-- 新建管理员弹出框 -->
      <el-dialog title="新建管理员" :visible.sync="dialogFormOneVisible">
        <el-form label-width="120px" label-position="left" :model="form_one_data" ref="form1" :rules="rules1"   >
          <el-form-item label="用户名称" class="ml15" prop="user_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.user_name"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="密码" class="ml15" prop="passwd">
            <el-col :span="12">
              <el-input v-model="form_one_data.passwd" type="password"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="重复密码" class="ml15" prop="repasswd" >
            <el-col :span="12">
              <el-input v-model="form_one_data.repasswd" type="password"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="联系电话" class="ml15" prop="telphone">
            <el-col :span="12">
              <el-input v-model="form_one_data.telphone"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="绑定公司" class="ml15" prop="relation_company_name" disable="true">
            <el-col :span="12">
              <el-input v-model="form_one_data.relation_company_name" :disabled="true"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="状态" class="ml15" prop="bus_type">
            <el-col :span="12">
              <el-radio v-model="form_one_data.status" label="1">启用</el-radio>
              <el-radio v-model="form_one_data.status" label="2">停用</el-radio>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormOneVisible = false">取 消</el-button>
          <el-button type="primary"  @click="add_admin">确 定</el-button>
        </div>
      </el-dialog>
    <!-- 新建公司 -->
     <el-dialog :title="title1" :visible.sync="dialogFormTwoVisible">
        <el-form label-width="120px" label-position="left"  :model="form_two_data" ref="form2" :rules="rules2" >
          <el-form-item label="公司名称" class="ml15" prop="company_name">
            <el-col :span="12">
              <el-input v-model="form_two_data.company_name"></el-input> 
            </el-col>
          </el-form-item>
           <el-form-item label="联系人" class="ml15" prop="company_contact">
            <el-col :span="12">
              <el-input v-model="form_two_data.company_contact"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="联系电话" class="ml15" prop="company_tel">
            <el-col :span="12">
              <el-input v-model="form_two_data.company_tel"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="备注" class="ml15" prop="company_remarks" disable="true">
            <el-col :span="12">
              <el-input v-model="form_two_data.company_remarks" type="textarea" ></el-input>
            </el-col>
          </el-form-item>
         
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormTwoVisible = false">取 消</el-button>
          <el-button type="primary" @click="add_company">确 定</el-button>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  import {companyLists,addCompanyAdmin,addCompany,detailCompany} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
    data(){
      return{
       
        title1:'新建公司',
        queryStr: {
          company_name:'',
          total: 1,       // 总数
          page: 1,        // 当前分页
          size: 10,      // 每页条数
        },
        form_one_data:{
          relation_company_name:'',
          relation_company_id:'',
          user_name:'',
          passwd:'',
          repasswd:'',
          telphone:'',
          status:"1",
        },
        rules1: {
          user_name: [
            { required: true, message: '请输入管理员名', trigger: 'blur' },
          ],
          passwd: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ],
          repasswd: [
            { required: true, message: '请输入确认密码', trigger: 'blur' },
          ],
          telphone: [
            { required: true, message: '请输入联系电话', trigger: 'blur' },
            { validator: tel_num, trigger: 'blur' },
          ],
          relation_company_name: [
            { required: true, message: '请绑定公司', trigger: 'blur' },
          ],
        },
        form_two_data:{
          company_name:'',
          company_contact:'',
          company_remarks:'',
          company_tel:'',
        },
        rules2: {
            company_name: [
                { required: true, message: '请输入公司名', trigger: 'blur' },
            ],
            company_tel: [
                { validator: tel_num, trigger: 'blur' },
            ],
            company_remarks: [
                { min: 0, max: 200, message: '长度在 0 到 200 个字符', trigger: 'blur' }
            ],
        },
        dialogFormOneVisible: false,
        dialogFormTwoVisible: false,
        dialogTableVisible: false,  
        tableData:[],
        tableTwoData:[
          {
            id:1,
            actionTime:"张三",
            username:"18600877033",
            actionType:0,
            remark:1,
          },
        ],
        loading: false,//loading
        centerDialogVisible: false,
  
      }
    },
    mounted(){
        this.query()
    },
    methods:{
        // 查询
        query(run_type) { 

            let params = {
                page: this.queryStr.page,
                limit: this.queryStr.limit,
                company_name:this.queryStr.company_name,
            };
            companyLists(params).then((res) => {
                this.total = Number(res.data.data.total);
                this.tableData = res.data.data.data;
            });
            
        },
        newAdd(){
            this.title1 = '新建公司';
            this.form_two_data={};
            this.dialogFormTwoVisible=true;
        },
        add_query(data,index){
            this.form_one_data.relation_company_id = data.id;
            this.form_one_data.relation_company_name = data.company_name;
            this.dialogFormOneVisible=true;
        },
        add_admin(){
            this.$refs['form1'].validate((valid) => {
                if (valid) {
                    this.dialogFormOneVisible =false;
                    let params  = this.form_one_data;
                    addCompanyAdmin(params).then((res) => {
                        this.$message.success('添加成功');
                        this.query();
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            
        },
        add_company(){
            this.$refs['form2'].validate((valid) => {
                if (valid) {
                    this.submitBefore();
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        submitBefore(){
            let params = this.form_two_data;
            addCompany(params).then((res) => {
                this.$message.success('执行成功');
                this.dialogFormTwoVisible = false;
                this.title1 = '新建公司';
                this.query();
            });
        },
        // 分页器
        currentChange(val) {
            this.queryStr.page = val;
            this.query();
          },
        // 提交
        submit_query(){
            this.$refs['form'].validate((valid) =>{
            this.flag = true;
                if (!valid) {
                    this.flag = false;
                    return false;
                }else{
                    if(this.query_dialog_type == 0){
                    issues_add(this.quesData).then(res => {
                        this.centerDialogVisible = false;
                        this.flag = false;
                        // this.start_end_time = [(new Date().getTime() - 60*1000), new Date().getTime()]
                    })
                    }else{
                    issues_edit(this.quesData).then(res => {
                        this.centerDialogVisible = false;
                        this.tableData[this.list_active_index] = Object.assign(this.tableData[this.list_active_index], this.quesData);
                        this.flag = false;
                    })
                    }
                    this.query();
                }
            })
        },
        // 修改 yjq
        edit_query(val, index){
            this.title1 = '编辑公司';
            let id = val.id;
            let params = {
                'id':id,
            }
            detailCompany(params).then((res) => {
                this.form_two_data = res.data.data;
                this.dialogFormTwoVisible = true;
            });
        },
        // 时间戳的转换
        changeTime(time){
            let year = new Date((time)).getFullYear();
            let month = new Date((time)).getMonth()+1 < 10?'0'+(new Date((time)).getMonth()+1):(new Date((time)).getMonth()+1);
            let date = new Date((time)).getDate() < 10 ?'0'+new Date((time)).getDate():new Date((time)).getDate();
            let hours = new Date((time)).getHours() < 10 ?'0'+new Date((time)).getHours():new Date((time)).getHours();
            let minutes = new Date((time)).getMinutes() < 10 ?'0'+new Date((time)).getMinutes():new Date((time)).getMinutes();
            let seconds = new Date((time)).getSeconds() < 10 ?'0'+new Date((time)).getSeconds():new Date((time)).getSeconds();
            return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
        }
    }
  }
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}

  
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





